  data(){
    return {
        historyNavLeft: 0,
      historyInnerLeft: 0
    }
  },
  methods: {
    prevClick() {
      this.historyNavLeft += 250;
      this.historyInnerLeft += 250;
      if (this.historyNavLeft > 0) {
        this.historyNavLeft = 0;
        this.historyInnerLeft = 0;
      }
    },
    nextClick() {
      this.historyNavLeft -= 250;
      this.historyInnerLeft -= 250;
      if (this.historyNavLeft < -1100) {
        this.historyNavLeft = -1100;
        this.historyInnerLeft = -1100;
      }
    }
  }
